<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations, mapGetters } = createNamespacedHelpers(
  "todoList"
);

export default {
  name: "Filters",
  data() {
    return {
      filters: ["ALL", "COMPLETED", "ACTIVE"]
    };
  },
  computed: {
    ...mapState(["filter"]),
    ...mapGetters(["completedTotal", "total"])
  },
  methods: {
    ...mapMutations(["toggle", "addTodo"])
  },
  render(h) {
    let _this = this;
    //render类似react中的render，也可以用jsx语法，参数h其实是createElement和React.createElement
    return (
      <div class="filters">
        {this.filters.map(item => {
          if (this.filter === item) {
            return (
              <el-button plain disabled>{item}</el-button>
            );
          }
          return (
            <el-button onClick={()=>{this.toggle(item)}} type="primary">{item}</el-button>
          );
        })}
        {this.completedTotal}/{this.total}
      </div>
    );
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.filters > * {
  margin-right: 5px;
  margin-top: 10px;
}

</style>
