import Ser from '../server/stuServer'
export default {
    namespaced: true,
    state: {
        currentPage: 1,
        eachPage: 5,
        movies: [],
        totalPage: 0,
        count: 0
    },
    mutations: {
        getStudentsByPage: (state, payload) => {
            Object.assign(state, payload)
            //这样也可以。因为我们不能把payload直接赋值给state.只有state身上的属性是响应式的。
            // for (let k in payload) {
            //     state[k] = payload[k]
            // }
        },
        setEachPage: (state, eachPage) => state.eachPage = eachPage,
        setCurrentPage: (state, currentPage) => state.currentPage = currentPage,
        // nextPage(state) {
        //     if (state.currentPage !== state.totalPage) {
        //         state.currentPage++
        //     }
        // },
        // prevPage(state) {
        //     if (state.currentPage !== 1) {
        //         state.currentPage--
        //     }
        // },
        // homePage(state) {
        //     if (state.currentPage !== 1) {
        //         state.currentPage = 1
        //     }
        // },
        // weiPage(state) {
        //     if (state.currentPage !== state.totalPage) {
        //         state.currentPage = state.totalPage
        //     }
        // }
    },
    actions: {
        async getStudentsByPageAsync(context) {
            const { currentPage, eachPage, count } = context.state
            var data = await Ser.getStudentsByPage({ currentPage, eachPage })
            // console.log(data);
            context.commit('getStudentsByPage', data)//将获取到的数据利用mutations更新到state中。
        }
    }
}