<template>
  <!-- <ul class="ulBox">
    <li
      :key="item.id"
      v-for="item in todos"
      :class="{completed:item.completed}"
      @click="completedTodo(item)"
    >
      {{item.text}}
      <i @click="removeItem(item)">删</i>
    </li>
  </ul>-->
  <el-table :data="todos" border style="width: 100%;margin-top:20px;" height="204px" :cell-style="cell" ref="multipleTable">
    <el-table-column prop="text" label="文本"></el-table-column>
    <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
        <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations, mapGetters } = createNamespacedHelpers(
  "todoList"
);

export default {
  name: "Todos",
  methods: {
    ...mapMutations(["completedTodo", "removeItem"]),
    handleEdit(index, row) {
      console.log(index, row);
      row.completed = !row.completed;
    },
    handleDelete(index, row) {
      row.flag = !row.flag;
    },
    cell({ row, column, rowIndex, columnIndex }) {
      // console.log(row, column, rowIndex, columnIndex);
      if (row.completed) {
        return { color: "red", 'text-decoration': 'line-through' };
      }
    }
  },
  computed: {
    // todos(){
    //   return this.$store.getters.filterData
    // }
    ...mapGetters({
      todos: "filterData"
    })
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->