<template>
  <div class="addtodo">
    <el-input placeholder="请输入内容" prefix-icon="el-icon-search" clearable v-model="input">
      <el-button slot="append" type="primary" @click="handleClick">AddTodo</el-button>
    </el-input>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations, mapGetters } = createNamespacedHelpers(
  "todoList"
);
export default {
  name: "AddTodo",
  data() {
    return {
      input: ""
    };
  },
  methods: {
    ...mapMutations(["addTodo"]),
    handleClick() {
      this.addTodo(this.input);
    },
    affirm(e) {
      if (e.code === "Enter") {
        this.handleClick();
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.addtodo {
  height: 30px;
  margin-bottom: 10px;
}
.addtodo > input {
  height: 100%;
  width: 65%;
  border-radius: 1ch;
  border: 0;
  padding: 0 15px;
}
.addtodo > button {
  height: 30px;
  margin-left: 20px;
  width: 67px;
  border-radius: 6px;
  background: #6771f0;
  color: aliceblue;
  border: none;
  cursor: pointer;
}
.addtodo > button:hover {
  background: rgba(0, 0, 0, 0.2);
  color: #6771f0;
}
</style>
