import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login.vue'
import Register from './views/Register.vue'
import Info from './views/Info.vue'
import Counter from './components/counter/Count.vue'
import TodoList from './components/routetodo/TodoList.vue'
import Movie from './components/movie/movies.vue'
Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/login/:username',
      name: 'LoginWithParams',
      component: Login
    },
    {
      path: '/reg',
      name: 'Register',
      component: Register
    },
    {
      path: '/info',
      name: 'Info',
      component: () => import(/* webpackChunkName: "info" */ './views/Info.vue'),
      beforeEnter: (to, from, next) => {
        // console.log('info路由独享守卫');
        next()//next控制是否允许进入该路径，接收参数，布尔值，默认true.
      },
      children: [{//配置子路由的地方
        path: 'counter',
        name: 'Counter',
        component: Counter
      }, {
        path: 'todoList',
        name: 'TodoList',
        component: TodoList
      }, {
        path: 'movies',
        name: 'Movie',
        component: Movie
      }]
    },
    // {
    //   path: '/about',
    //   name: 'about',
    //   // route level code-splitting
    //   // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    //   //实现延迟加载，也就是进入该路径才加载，优化性能的。
    //   component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    // }
  ]
})

//全局路由守卫，当任何一个路径发生变化时就会触发，
router.beforeEach((to, from, next) => {
  // console.log(to, from);
  next()
})
router.afterEach((to, from) => {
  // console.log(to, from, "全局后置守卫。");

})
export default router