
<template>
  <div>
    <el-table border stripe :data="movies">
      <el-table-column prop="_id" label="编号"></el-table-column>
      <el-table-column prop="cName" label="电影"></el-table-column>
      <el-table-column prop="directors" label="主演"></el-table-column>
      <el-table-column prop="type" label="类型"></el-table-column>
      <el-table-column prop="country" label="国家"></el-table-column>
      <el-table-column prop="duration" label="片长"></el-table-column>
      <el-table-column prop="release" label="上映时间"></el-table-column>
    </el-table>
    <el-pagination
      @size-change="setEachPage"
      @current-change="setCurrentPage"
      :current-page="currentPage"
      :page-sizes="[5, 9, 13,18 ]"
      :page-size="eachPage"
      layout="total, sizes, prev, pager, next, jumper"
      :total="count"
    ></el-pagination>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const {
  mapState,
  mapMutations,
  mapActions,
  mapGetters
} = createNamespacedHelpers("movies");
export default {
  watch: {
    //使用侦听器watch监听eachPage,currentPage这两个属性的变化
    eachPage() {
      this.currentPage = 1;
      this.getStudentsByPageAsync();
    },
    currentPage() {
      this.getStudentsByPageAsync();
    }
  },
  computed: {
    //因为计算属性，默认只有get方法，所以我们给eachPage,currentPage属性单独设置set用以实现数据双向绑定
    ...mapState(["movies", "totalPage", "count"]),
    eachPage: {
      get: mapState(["eachPage"]).eachPage,
      set: mapMutations(["setEachPage"]).setEachPage
    },
    currentPage: {
      get: mapState(["currentPage"]).currentPage,
      set: mapMutations(["setCurrentPage"]).setCurrentPage
    }
  },
  methods: {
    ...mapActions(["getStudentsByPageAsync"]),
    ...mapMutations(["setCurrentPage", "setEachPage"])
  },
  mounted() {
    this.getStudentsByPageAsync();
  }
};
</script>


<style scoped>
tr,
td {
  border: 1px solid #321;
  text-align: center;
}
</style>
