<template>
  <div class="login">
    <h1>登录页面</h1>
    <div class="d1">
      <el-input v-model="phone" placeholder="请输入内容"></el-input>
    </div>
    <div class="d2">
      <el-input placeholder="请输入密码" v-model="pwd" show-password></el-input>
    </div>
    <div class="d3">
      <el-button type="primary" plain @click="Login">登陆</el-button>
      <el-button type="primary" plain @click="Reg">注册</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      phone: "",
      pwd: ""
    };
  },
  mounted() {
    this.phone = this.$route.params.username    
  },
  methods: {
    Login() {
      this.$router.push("/info");
    },
    Reg() {
      this.$router.push("/reg");
    }
  }
};
</script>

<style scoped>
.login {
  width: 400px;
  margin: 50px auto;
  border: #409eff 1px solid;
  padding: 20px;
  border-radius: 1ch;
}
h1 {
  margin: 0 0 10px 0;
  text-align: center;
  color: #409eff;
}
.d2,
.d1 {
  margin-bottom: 20px;
}
</style>

