<template>
  <div>
    <el-card class="box-card">
      <div class="box">
        <el-input v-model="input" placeholder="placeholder"></el-input>
        <el-button-group>
          <el-button type="primary" @click="add" icon="el-icon-arrow-left">加</el-button>
          <el-button @click="minus" type="primary">
            减
            <i class="el-icon-arrow-right el-icon--right"></i>
          </el-button>
        </el-button-group>
      </div>
      <h1>you click {{count}} times!</h1>
    </el-card>
  </div>
</template>


<script>
//作用是辅助我们获取状态或者方法。
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations, mapGetters } = createNamespacedHelpers(
  "counter"
);
export default {
  data() {
    return {
      input: ""
    };
  },
  computed: {
    // count() {   上面用到了这么多状态意味着就要写这么多个获取的方法，就很麻烦，我们使用辅助函数。
    //   return this.$store.state.count;
    // },
    // a() {
    //   return this.$store.state.a;
    // },
    // b() {
    //   return this.$store.state.b;
    // },
    // c() {
    //   return this.$store.state.c;
    // }
    ...mapState(["count", "a", "b"]),
    //我们也可以使用别名。比如上面用到了c1但是想用我们的c属性的值。
    ...mapState({ c1: "c" })
  },
  methods: {
    // add() {
    //   this.$store.commit("add");
    // },
    // minus() {
    //   this.$store.commit("minus");
    // }
    //这里和上面一样
    ...mapMutations(["add", "minus"])
  }
};
</script>


<style scoped>
.box-card {
  width: 500px;
  margin: 50px auto;
}

</style>