
<template>
  <div class="reg">
    <h1>注册页面</h1>
    <div class="d1">
      <el-input ref="reg" v-model="phone" placeholder="请输入内容"></el-input>
    </div>
    <div class="d2">
      <el-input placeholder="请输入密码" v-model="pwd" show-password></el-input>
    </div>
    <div class="d3">
      <el-button type="primary" plain @click="reg($refs.reg.value)">注册</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Reg",
  data() {
    return {
      phone: "",
      pwd: ""
    };
  },
  methods: {
    reg(params) {
      this.$router.push(`/login/${params}`);
    }
  }
};
</script>

<style scoped>
.reg {
  width: 400px;
  margin: 50px auto;
  border: #409eff 1px solid;
  padding: 20px;
  border-radius: 1ch;
}
h1 {
  margin: 0 0 10px 0;
  text-align: center;
  color: #409eff;
}
.d2,
.d1 {
  margin-bottom: 20px;
}
</style>